/* ICON BUTTON */

.icon-button {
  transition: all 200ms;
  cursor: pointer;
}

.icon-button--primary {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 2.4rem;
  height: 2.4rem;
  background-color: hsl(200, 20%, 30%);
  background-color: var(--color-grey-dark);

  font-size: 1.8rem;
  color: hsl(200, 35%, 95%);
  color: var(--color-grey-lighter);

  border-radius: 0.25rem;
}

.icon-button--primary:active {
  background-color: hsl(200, 20%, 20%);
  background-color: var(--color-grey-darker);
}

.icon-button--secondary {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 1.5rem;
  height: 1.5rem;
  border: 1px solid hsl(200, 65%, 50%);
  border: 1px solid var(--color-primary);

  color: hsl(200, 65%, 50%);

  color: var(--color-primary);
  font-size: 1.4rem;

  border-radius: 0.25rem;
}

.icon-button--secondary:hover {
  background-color: hsla(200, 65%, 50%, 0.1);
  background-color: var(--color-primary-transparent-1);
}

.icon-button--secondary:active {
  background-color: hsla(200, 65%, 50%, 0.2);
  background-color: var(--color-primary-transparent-2);
  box-shadow: 0 0 0 2px hsla(200, 65%, 50%, 0.5);
  box-shadow: 0 0 0 2px var(--color-primary-transparent-5);
}

.icon-button--tertiary {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 1.8rem;
  height: 1.8rem;

  color: hsl(200, 20%, 30%);

  color: var(--color-grey-dark);
  font-size: 1.6rem;

  border-radius: 0.25rem;
}

.icon-button--tertiary:hover {
  background-color: hsla(200, 65%, 50%, 0.1);
  background-color: var(--color-primary-transparent-1);
}

.icon-button--tertiary:active {
  background-color: hsla(200, 65%, 50%, 0.2);
  background-color: var(--color-primary-transparent-2);
  box-shadow: 0 0 0 2px hsla(200, 65%, 50%, 0.5);
  box-shadow: 0 0 0 2px var(--color-primary-transparent-5);
}

.icon-button--tertiary:disabled,
.icon-button--secondary:disabled {
  cursor: not-allowed;
}

.icon-button--secondary:disabled {
  border: 1px solid hsl(200, 20%, 60%);
  border: 1px solid var(--color-grey);
  color: hsl(200, 20%, 60%);
  color: var(--color-grey);
}

.icon-button--secondary:disabled:hover,
.icon-button--secondary:disabled:active,
.icon-button--tertiary:disabled:hover,
.icon-button--tertiary:disabled:active {
  background-color: hsla(200, 20%, 10%, 0.1);
  background-color: var(--color-black-transparent-1);
  box-shadow: none;
}

.icon-button--quaternary {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 2.4rem;
  height: 2.4rem;

  color: hsl(200, 20%, 30%);

  color: var(--color-grey-dark);
  font-size: 2.2rem;

  border-radius: 0.25rem;
}

/* BUTTON */

.button {
  position: relative;
  overflow: hidden;
  height: 2.5rem;
  border-radius: 0.25rem;
  cursor: pointer;
  transition: all 300ms;
  font-size: 1.2rem;
  font-weight: 400;
  white-space: nowrap;
}

.button--contained {
  background-color: hsl(200, 65%, 55%);
  background-color: var(--color-primary-light);
  color: hsl(200, 35%, 95%);
  color: var(--color-grey-lighter);
}

.button--contained:active {
  background-color: hsl(200, 65%, 65%);
  background-color: var(--color-primary-lighter);
}

.button--contained:disabled {
  background-color: hsl(200, 35%, 85%);
  background-color: var(--color-grey-light);
  cursor: not-allowed;
}

.button--outlined {
  color: hsl(200, 65%, 50%);
  color: var(--color-primary);
  border: 2px solid hsl(200, 65%, 50%);
  border: 2px solid var(--color-primary);
}

.button--outlined:hover {
  background-color: hsla(200, 65%, 50%, 0.1);
  background-color: var(--color-primary-transparent-1);
}

.button--outlined:active {
  background-color: hsla(200, 65%, 50%, 0.2);
  background-color: var(--color-primary-transparent-2);
}

.button__loading-spinner {
  margin: 0 auto;
}

.button:hover .button__text {
  transform: translate(-50%, 5rem);
}

.button:hover .button__icon {
  transform: translate(-50%, -50%);
}

/* LOADING SPINNER */

.loading-spinner {
  border: 0.15rem solid hsl(200, 65%, 55%);
  border: 0.15rem solid var(--color-primary-light);
  border-left-color: hsl(200, 35%, 95%);
  border-left-color: var(--color-grey-lighter);
  border-radius: 50%;

  animation: spin 1s linear infinite;
}

.loading-spinner--sm {
  width: 1.25rem;
  height: 1.25rem;
}

.loading-spinner--med {
  width: 1.5rem;
  height: 1.5rem;
}

.loading-spinner--lg {
  width: 1.75rem;
  height: 1.75rem;
}

/* ICON LINK */

.icon-link svg {
  width: 3.8rem;
  height: 3.8rem;
  fill: hsl(200, 20%, 30%);
  fill: var(--color-grey-dark);
  transition: transform 300ms;
  cursor: pointer;
}

.icon-link:hover svg {
  fill: hsl(200, 20%, 20%);
  fill: var(--color-grey-darker);
  transform: scale(1.15);
}

/* ANIMATIONS */

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

.fade-enter,
.fade-exit-active {
  opacity: 0;
}

.fade-enter-active {
  opacity: 1;
}

/* INPUTS */

.input,
.stripe-input {
  padding: 0.5rem;
  padding: var(--space-med);
  background-color: #fff;
  background-color: var(--color-white);
  color: hsl(200, 20%, 20%);
  color: var(--color-grey-darker);
  border-radius: 0.25rem;
  border: 1px solid hsl(200, 35%, 85%);
  border: 1px solid var(--color-grey-light);

  transition: all 300ms;
}

.input:hover,
.stripe-input:hover {
  border: 1px solid hsl(200, 20%, 60%);
  border: 1px solid var(--color-grey);
}

.input:focus,
.stripe-input--focus {
  border: 1px solid hsl(200, 65%, 50%);
  border: 1px solid var(--color-primary);
  box-shadow: 0 0 0 1px hsl(200, 65%, 50%);
  box-shadow: 0 0 0 1px var(--color-primary);
}

.input--invalid,
.input--invalid:focus,
.input--invalid:hover,
.stripe-input--invalid,
.stripe-input--invalid:focus,
.stripe-input--invalid:hover {
  border: 1px solid tomato;
  box-shadow: 0 0 0 1px tomato;
}

